@font-face {
  font-family: 'Calibrepro';
  font-weight: 400;
  src: url("../fonts/CalibreRegular.otf");
}

@font-face {
  font-family: 'Calibrepro';
  font-weight: 500;
  src: url("../fonts/CalibreSemibold.otf");
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.container {
  padding-top: 130px;
  width: 100%;
  display: flex;
  justify-content: center;
}

body {
  font-family: "Calibrepro";
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}

.alert-warning {
  color: red;
  font-weight: 600;
  padding-top: 10px;
}

.alert-success {
  color: #58c93a;
  font-weight: 600;
  padding-top: 10px;
}

.avatar_image {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  display: block;
  border: 5px solid #58c93a;
  margin: auto;
  margin-bottom: 10px;
}

.delete {
  cursor: pointer;
}

.photoAdmin {
  border: 5px solid #d6d60f !important;
}

.info-user {
  text-align: center;
}

.text-bold {
  font-weight: 600;
}

.center-text {
  text-align: center;
  text-transform: uppercase;
}

.pagination {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  width: 100%;
  border-top: 1px solid #e1e0e0;
  padding-bottom: 50px;
}

.pagination .disabled {
  color: #818181;
  text-transform: uppercase;
  font-size: 18px;
}

.pagination li {
  padding-top: 20px;
}

.pagination li a {
  font-size: 18px;
  text-transform: uppercase;
  color: #a8a8a8;
}

.info-user li {
  display: block;
  padding: 0px;
}

.info-user li a {
  display: block;
  padding: 10px;
  background: #f8f8f8;
  color: #58c93a;
}

p {
  margin: 0px;
}

input[type=text], input[type=password], input[type=email] {
  padding: 10px;
  width: 100%;
  border: 1px solid #e1e0e0;
}

input[type=text]:focus, input[type=password]:focus, input[type=email]:focus, textarea {
  outline: none;
  margin-top: 5px;
  border: 1px solid #58c93a;
}

input[type=submit] {
  padding: 5px;
}

::placeholder {
  color: #e1e0e0;
}

a {
  text-decoration: none;
}

section {
  width: 48%;
}

aside {
  width: 20%;
  margin-left: 50px;
}

aside span {
  margin-left: 10px;
}

aside .fa {
  font-size: 25px;
}

aside a {
  text-decoration: none;
  font-weight: 600;
}

aside .logout {
  padding: 5px;
  color: #58c93a;
  background: #292929;
}

aside ul {
  margin: 0px;
}

aside li {
  list-style: none;
  padding: 5px;
  margin-bottom: 10px;
  display: inline-block;
}

aside div h2 {
  text-transform: uppercase;
  background: #f8f8f8;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  border-left: 4px solid #58c93a;
  font-size: 18px;
  font-weight: 600;
  color: #adacac;
  margin-bottom: 16px;
}

aside div a {
  align-self: flex-end;
}

aside div .content {
  display: flex;
  justify-content: space-between;
}

header {
  background: #292929;
  width: 100%;
  height: 65px;
  position: relative;
  position: fixed;
}

header span {
  color: #58c93a;
}

header h1 {
  color: white;
  display: block;
  cursor: pointer;
  transition: 300ms;
  line-height: 65px;
  background: #292929;
}

header h1:after {
  content: ".";
  color: #58c93a;
  transition: 300ms;
}

header h1:hover:after {
  color: white;
}

header nav a:hover {
  background: gray;
}

header nav {
  display: flex;
  justify-content: space-around;
  transition: 1s;
}

header nav ul {
  list-style-type: none;
  padding: 0;
}

header nav a {
  padding: 0 15px;
  border-left: 1px solid #1d1d1d;
  transition: 0.8s;
  cursor: pointer;
  line-height: 65px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  color: white;
  background: #292929;
}

header nav a:hover {
  color: #58c93a;
}

header nav a {
  border-left: 1px solid #1d1d1d;
  border-right: 1px solid #1d1d1d;
}

header nav li {
  color: white;
  text-decoration: none;
  display: inline-block;
}

#hamburguesa {
  position: absolute;
  left: 10px;
  top: 10px;
  display: none;
}

footer {
  background: #f8f8f8;
  padding-top: 100px;
  padding-bottom: 100px;
  /* Add a hover effect if you want */
  /* Set a specific color for each brand */
  /* Facebook */
  /* Twitter */
}

footer h2 {
  padding-bottom: 40px;
  text-align: center;
}

footer p {
  text-align: center;
  padding-bottom: 40px;
  color: #e2e2e2;
}

footer span {
  color: #b5b5b5;
  font-weight: 500;
}

footer .fa {
  padding: 10px;
  font-size: 20px;
  margin-right: 10px;
  text-align: center;
  text-decoration: none;
}

footer .fa:hover {
  opacity: 0.7;
}

footer .fa-facebook {
  background: #3b5998;
  color: white;
}

footer .fa-twitter {
  background: #55acee;
  color: white;
}

footer .fa-linkedin {
  background: #007bb5;
  color: white;
}

footer .fa-instagram {
  background: #125688;
  color: white;
}

footer div {
  text-align: center;
}

@media only screen and (max-width: 800px) {
  .container {
    flex-direction: column;
    padding-right: 10px;
    padding-left: 10px;
  }
  section {
    width: 100%;
    display: block;
  }
  aside {
    width: 100%;
    flex-direction: column;
    margin: 0px;
  }
  aside div {
    width: 90%;
    margin: auto;
  }
  #hamburguesa {
    display: block;
    cursor: pointer;
  }
  h1 {
    text-align: center;
  }
  nav a {
    text-align: center;
  }
  nav {
    position: absolute;
    top: 65px;
    flex-direction: column;
    left: -130px;
  }
  li {
    display: block;
  }
}
