@font-face {
  font-family: 'Calibrepro';
  font-weight: 400;
  src: url("../fonts/CalibreRegular.otf");
}

@font-face {
  font-family: 'Calibrepro';
  font-weight: 500;
  src: url("../fonts/CalibreSemibold.otf");
}

.page-user section {
  padding: 10px;
}

.page-user label {
  font-weight: 600;
}

.page-user input {
  margin-top: 10px;
}

.page-user article h1 {
  font-size: 28px;
  line-height: 30px;
  padding-bottom: 55px;
  color: #4f4e4e;
}

.page-user article div {
  padding: 15px;
}

.page-user article img {
  width: 100%;
  object-fit: cover;
  height: 306px;
  padding-bottom: 25px;
}

.page-user article p {
  font-size: 16px;
  line-height: 20px;
  color: #606060;
  padding-bottom: 15px;
}

.page-user article a {
  color: #606060;
  line-height: 15px;
}

.page-user article .btn-read {
  padding: 5px 15px 5px 15px;
  border-radius: 25px;
  border: 1px solid #606060;
  color: #c6c5c5;
}

.page-user article .pagination {
  border-top: 1px solid #e1e0e0;
  width: 100%;
}
